// [ui-view]
$ui-view__transition : 0.2s opacity linear,
0.2s transform linear !default;
// .ng-enter
$ui-view__ng-enter__opacity: 0 !default;
$ui-view__ng-enter__transform: scale3d(.98, .98, .98) !default;
// .ng-enter-active
$ui-view__ng-enter-active__opacity: 1 !default;
$ui-view__ng-enter-active__transform: scale3d(1, 1, 1) !default;
// .ng-leave
$ui-view__ng-leave__opacity: 0 !default;
$ui-view__ng-leave__transform: scale3d(0, 0, 0) !default;


.main-wrapper {
    &__container {
        position: relative;
    }
    [ui-view=""],
    [ui-view="service"] {
        width: 100%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        &.ng-enter,
        &.ng-leave {
            transition: $ui-view__transition;
        }
        &.ng-enter {
            opacity: $ui-view__ng-enter__opacity;
            transform: $ui-view__ng-enter__transform;
        }
        &.ng-enter-active {
            opacity: $ui-view__ng-enter-active__opacity;
            transform: $ui-view__ng-enter-active__transform;
        }
        &.ng-leave {
            opacity: $ui-view__ng-leave__opacity;
            transform: $ui-view__ng-leave__transform;
        }
        &.ng-leave-active {}
    }
}