/* Headings */
/****
.primary-heading {
    font-size: $primary-heading__font-size;
    font-weight: $primary-heading__font-weight;
    text-transform: $primary-heading__text-transform;
    @include after {
        content: "";
        width: $primary-heading__after__width;
        height: $primary-heading__after__height;
        background: $primary-heading__after__background;
        display: block;
        font-size: $primary-heading__after__font-size;
        border-radius: $primary-heading__after__border-radius;
        margin: $primary-heading__after__margin;
    }
}

.secondary-heading {
    font-size: $secondary-heading__font-size;
}

.tertiary-heading {
    font-size: $tertiary-heading__font-size;
}

.secondary-heading,
.tertiary-heading,
.quaternary-heading {
    font-weight: $headings__font-weight;
    padding-bottom: $headings__padding-bottom;
    border-bottom: $headings__border-bottom;
}

.quaternary-heading {
    font-size: $quaternary-heading__font-size;
    border-bottom: 0;
    margin: 0;
}
****/
/* Text */
.text-small {
  font-size: 0.85em; }

.text-normal {
  font-size: 1em; }

.text-medium {
  font-size: 1.15em; }

.text-large {
  font-size: 1.3em; }

.text-center {
  text-align: center !important; }

.text-left {
  text-align: left !important; }

.text-right {
  text-align: right !important; }

.text-hidden {
  line-height: 0;
  font-size: 0;
  text-shadow: none;
  color: transparent; }

.text-muted {
  color: #757575; }

strong {
  font-weight: 500; }

/****
.icon {
    display: inline-block;
    &-dashboard {
        &__white {
            @include sprite($home-white);
        }
        &__black {
            @include sprite($home-black);
        }
    }
    &-myaccount {
        &__white {
            @include sprite($account-circle-white);
        }
        &__black {
            @include sprite($account-circle-black);
        }
    }
    &-logout {
        &__white {
            @include sprite($exit-to-app-white);
        }
        &__black {
            @include sprite($exit-to-app-black);
        }
    }
}
