.register-form__terms-conditions {
  padding-bottom: 20px;
  margin: 0; }

.services-list {
  padding: 0; }
  .services-list__list-item, .services-list__list-item._md {
    align-items: stretch;
    padding: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05); }
    .services-list__list-item:last-child, .services-list__list-item._md:last-child {
      border-bottom: 0; }
  .services-list__link {
    line-height: 48px;
    flex: 1;
    white-space: nowrap;
    padding: 0 25px;
    transition: .2s background-color ease, .2s color ease, .1s border ease; }
    .services-list__link, .services-list__link:link, .services-list__link:visited {
      color: rgba(255, 255, 255, 0.85); }
    .services-list__link:hover {
      background-color: rgba(0, 0, 0, 0.05);
      color: #fff; }
    .services-list__link-active {
      border-left: 3px solid #ffc107; }

