.breadcrumb {
  list-style: none;
  padding: 16px;
  margin: 0; }
  .breadcrumb li {
    display: inline;
    font-size: 0.8em; }
    .breadcrumb li:not(:last-child)::after {
      content: "\002F";
      margin: 0 15px; }

.flat-panel {
  background-color: #fff;
  color: #212121;
  margin-top: 25px;
  margin-bottom: 25px; }
  .flat-panel:nth-child(n+2) {
    margin-left: 20px; }
    @media screen and (max-width: 959px) {
      .flat-panel:nth-child(n+2) {
        margin: 0; } }
  .flat-panel__toolbar, .flat-panel__toolbar._md, .flat-panel__body {
    background-color: transparent;
    color: inherit; }
  .flat-panel__header {
    border-bottom: 1px solid #bdbdbd; }
  .flat-panel__item-list, .flat-panel__item-list._md {
    padding: 0; }

a {
  text-decoration: none;
  color: #0069c5;
  transition: 0.25s color ease; }
  a:link, a:visited {
    color: #0069c5; }
  a:hover {
    color: #009688; }
  a:active {
    color: #00796b; }

.btn {
  color: #fff;
  padding: 0 15px; }
  .btn-primary, .btn-primary:not([disabled]):hover, .btn-primary:not([disabled]).md-focused, .btn-primary[disabled] {
    background-color: #ffc107; }
  .btn-warn, .btn-warn:not([disabled]):hover, .btn-warn:not([disabled]).md-focused, .btn-warn[disabled] {
    background-color: #ff5722; }
  .btn-facebook, .btn-facebook:not([disabled]):hover, .btn-facebook:not([disabled]).md-focused, .btn-facebook[disabled] {
    background-color: #3b5998; }
  .btn-google, .btn-google:not([disabled]):hover, .btn-google:not([disabled]).md-focused, .btn-google[disabled] {
    background-color: #dd4b39; }

