.language-settings__list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  padding: 15px 0 0;
  margin: 0; }

.language-settings__link {
  line-height: 1.3;
  display: block;
  font-size: 0.8em;
  text-transform: uppercase;
  padding: 0.5em 1em;
  border: 1px solid #bdbdbd;
  transition: all 0.25s ease; }
  .language-settings__link, .language-settings__link:link, .language-settings__link:visited {
    color: #757575; }
  .language-settings__link-active {
    background-color: #00aa9a;
    border: 0;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), inset 0 0 8px rgba(30, 40, 50, 0.5); }
    .language-settings__link-active, .language-settings__link-active:link, .language-settings__link-active:visited {
      color: #fff; }

.user-panel {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center; }
  .user-panel__user-image {
    background-color: #ffc107;
    border-radius: 50%; }
  .user-panel__user-fullname {
    margin-left: 10px; }
  .user-panel__icon-button {
    background-image: url(../images/sprite.png);
    background-position: -4px -52px; }

.form-view__container {
  width: 360px; }

.helpdesk-view__container {
  position: relative; }

.helpdesk-view__icon-button {
  background-image: url(../images/sprite.png);
  background-position: -44px 4px;
  position: absolute;
  left: 0;
  top: 51px; }

.helpdesk-view__sidenav {
  width: 280px;
  background-color: #00796b; }

.helpdesk-view__container, .helpdesk-view__sidenav {
  min-height: calc(100vh - 65px); }

.service-view__container {
  height: 100%;
  position: relative; }

.service-view__toolbar, .service-view__toolbar._md {
  background-color: transparent;
  color: #212121; }

.service-view__toolbar-tools {
  transition: .15s padding-left linear; }
  @media screen and (max-width: 959px) {
    .service-view__toolbar-tools {
      padding-left: 80px; } }

.service-view__toolbar, .service-view__toolbar._md, .service-view__toolbar-tools {
  min-height: 68px;
  height: 68px; }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  line-height: 1.2; }

body {
  font-size: 1em;
  color: #212121; }

.clearfix::before, .clearfix::after {
  content: "";
  display: table; }

.clearfix::after {
  clear: both; }

.main-header__title {
  padding: 25px 0 18px; }

.main-header__brand-logo {
  max-width: 100px;
  display: block;
  margin: 0 auto; }

.authorized .main-header {
  height: 65px;
  background: #fff;
  position: relative;
  display: flex;
  z-index: 10;
  padding: 0 8px; }
  .authorized .main-header > * {
    flex: 1; }
  .authorized .main-header__title {
    display: flex;
    align-items: center;
    order: -1;
    padding: 0;
    margin: 0; }
  .authorized .main-header__brand-logo {
    max-width: 80px;
    margin: 0 0 0 92px; }
    @media screen and (max-width: 645px) {
      .authorized .main-header__brand-logo {
        margin: 0 0 0 22px; } }

.main-wrapper__container {
  position: relative; }

.main-wrapper [ui-view=""],
.main-wrapper [ui-view="service"] {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }
  .main-wrapper [ui-view=""].ng-enter, .main-wrapper [ui-view=""].ng-leave,
  .main-wrapper [ui-view="service"].ng-enter,
  .main-wrapper [ui-view="service"].ng-leave {
    transition: 0.2s opacity linear, 0.2s transform linear; }
  .main-wrapper [ui-view=""].ng-enter,
  .main-wrapper [ui-view="service"].ng-enter {
    opacity: 0;
    transform: scale3d(0.98, 0.98, 0.98); }
  .main-wrapper [ui-view=""].ng-enter-active,
  .main-wrapper [ui-view="service"].ng-enter-active {
    opacity: 1;
    transform: scale3d(1, 1, 1); }
  .main-wrapper [ui-view=""].ng-leave,
  .main-wrapper [ui-view="service"].ng-leave {
    opacity: 0;
    transform: scale3d(0, 0, 0); }

.main-footer {
  width: 280px;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 100;
  padding: 0; }
  .main-footer__copyright {
    font-size: 0.8em;
    font-weight: 400; }
  @media screen and (max-width: 959px) {
    .main-footer {
      display: none; } }

.authorized .main-footer__paragraph {
  color: #fff; }
